import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { InputNumber } from "primereact/inputnumber";
import { InputSwitch } from "primereact/inputswitch";
import { InputText } from "primereact/inputtext";
import { Message } from "primereact/message";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { taxActions } from "../../store/tax/taxSlice";
import { IAppState } from "../../models/appState";
import { ITax } from "../../models/tax";
import { v4 as uuidv4 } from "uuid";

const TaxEdit: React.FC = () => {
  const defaultItem: ITax = {
    id: "",
    name: "",
    value: 0,
    status: false,
    default: false,
  };
  const navigateTo = "/taxes";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [dirty, setDirty] = useState(false); // if form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const params = useParams();
  const memo = useRef(true);
  const [tax, isFetching, isError, taxes] = useAppSelector(
    (state: IAppState) => [
      state.taxes.tax,
      state.taxes.isFetching,
      state.taxes.isError,
      state.taxes.taxes,
    ],
    shallowEqual
  );
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(taxActions.getTaxById(id));
        }
      }
    }
  }, [params, dispatch]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== tax?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(tax);
    }
  }, [params, item, tax, isFetching, isError]);

  const isNameValid = useCallback(
    (name: string) => {
      let result = true;
      if (item.id.length === 0) {
        if (taxes.find((x) => x.name?.toLowerCase() === name?.toLowerCase())) {
          result = false;
        }
      } else {
        if (
          taxes.find(
            (x) =>
              x.name?.toLowerCase() === name?.toLowerCase() && x.id !== item.id
          )
        ) {
          result = false;
        }
      }

      return result;
    },
    [item.id, taxes]
  );

  useEffect(() => {
    if (!dirty) {
      return;
    }

    let isValid = true;
    for (const [key, value] of Object.entries(item)) {
      if (key === "id") {
        continue;
      }

      if (typeof value === "string") {
        if (value?.trim().length === 0) {
          isValid = false;
          break;
        }
      } else if (typeof value === "number") {
        if ((value || 0) < 0) {
          isValid = false;
          break;
        }
      }
    }

    setValid(isValid && isNameValid(item.name || ""));
  }, [dirty, item, isNameValid]);

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    setItem({ ...item, [tname]: tvalue });
    if (tname === "name") {
      const isValidName = isNameValid(tvalue);
      setErrors({
        ...errors,
        nameInvalid: !isValidName,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    } else if (tname !== "status") {
      setErrors({
        ...errors,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    }
  };

  const onInputNumberChange = (e: any) => {
    setDirty(true);
    const newValue: number = e.value === null ? -1 : e.value;
    setItem({ ...item, value: newValue });
    setErrors({ ...errors, value: newValue < 0 });
  };

  const onSaveClick = async () => {
    if (subtitle !== "Edit") {
      item.id = uuidv4();
    }

    dispatch(taxActions.updateTax(item));
    setTimeout(() => {
      dispatch(taxActions.getTaxes());
    }, 300);
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  return (
    <>
      <h3 className="caption">Tax: {subtitle}</h3>
      <div className="grid">
        <div className="col-12">
          <span className="field-required">*</span> - Required field
        </div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Tax</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="TaxName">
                  Tax Name <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="TaxName"
                  name="name"
                  maxLength={50}
                  placeholder="Tax"
                  value={item.name}
                  onChange={onChange}
                />
                {(errors as any)?.name && (
                  <Message severity="error" text="Tax Name is required." />
                )}
                {(errors as any)?.nameInvalid && (
                  <Message severity="error" text="Tax Name already exists." />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="TaxValue">
                  Tax Value (%)
                  <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputNumber
                  id="TaxValue"
                  name="value"
                  value={item.value}
                  maxFractionDigits={4}
                  placeholder="4.5"
                  onValueChange={(e) => onInputNumberChange(e)}
                />
                {(errors as any)?.value && (
                  <Message severity="error" text="Tax Value is required." />
                )}
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Status">
                  Status <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <div className="p-fixed-switch">
                  <InputSwitch
                    id="Status"
                    name="status"
                    checked={item.status}
                    value="0"
                    onChange={onChange}
                  />
                </div>
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">
                <div className="p-fixed-switch">&nbsp;</div>
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
        <div className="col-12">
          <Button
            icon="pi pi-check"
            className="p-button-rounded p-button-outlined"
            tooltip="Save"
            disabled={!valid}
            onClick={onSaveClick}
          />
          &nbsp; &nbsp;
          <Button
            icon="pi pi-times"
            className="p-button-rounded p-button-outlined"
            tooltip="Back to List"
            onClick={onCancelClick}
          />
        </div>
      </div>
    </>
  );
};

export default TaxEdit;
